.films-filters-header {
  background-color: var(--ff-dark-red);
  color: var(--ff-white);
}

.films-filters-body {
  background-color: var(--ff-charcoal);
  color: var(--ff-white);
}

.btn-success {
  color: var(--ff-white);
  background-color: var(--ff-dark-red);
  border-color: var(--ff-dark-red);
}

.btn-success:hover, .btn-success:focus {
  color: var(--ff-white);
  background-color: var(--ff-dark-red);
  border-color: var(--ff-dark-red);
}