.item {
  outline: auto;
  outline-color: var(--ff-dark-red);
  width: 100%;
  background-color: var(--ff-dark-red);
}


.linkButton {
  text-decoration: none !important;
}

.image-text {
  background-color: var(--ff-dark-red);;
  color: white;
}

.placeholder {
  width: 100%;
  height: auto;
  min-height: 130px;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url('https://upload.wikimedia.org/wikipedia/commons/c/c2/No_image_poster.png');
}

.img-resp {
  width: 100%;
  height: auto;
}

.imdb {
  background-color: #f3ce13;
  color: black;
}

.gold {
  color: gold;
}

@media (width < 576px) {
  .item {
    min-width: 100px;
  }
  .image-text {
    font-size:x-small
  }
}

