.flex-container {
  display: flex;
  flex-flow: column;
  height: 100%;
}

.flex-content {
  flex: 1 1 auto;
}

.bg-cover {
  background-image: url("../images/Background-Narrow.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-color: black;
}

