  background-blend-mode: saturation;
  background-attachment: fixed;
}
*/

.bg-darkgrey {
  background-color: darkgrey;
}

.fade-in {
  animation: fadeIn ease 3s;
}

.clickable {
  cursor: pointer;
}

.hyperlink {
  text-decoration: none;
  color: white;
}

@keyframes fadeIn{
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}