.paginator, .page-link {
  background-color: var(--ff-dark-red);
  color: var(--ff-white);
}

.page-link {
  color: var(--ff-white);
  border-color: var(--ff-dark-red);
  background-color: var(--ff-dark-red);
}

.page-item {
  cursor: pointer;
}

.active > .page-link {
  border-color: var(--ff-brown-red) !important;
  background-color: var(--ff-brown-red) !important;
  color: var(--ff-white);
}